import {fetchChart, fetchRoot
} from '../api/index.js';
import axios from 'axios';
export default{
    FETCH_CHART({commit}) {
        return fetchChart()
            .then(response => {
                commit('SET_CHART',response.data);
                return response;
            })
            .catch()
    },
    FETCH_ROOT({commit}){
        axios.get(fetchRoot())
            .then(response => {
                commit('SET_ROOT', response.data);
                return response;
            })
            .catch()
    }
}

